___CSS_LOADER_EXPORT___.push([module.id, `*{
  padding: 0;
  margin: 0;
}

@font-face {
  font-family: 'MyFont';
  src:url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('woff');
  font-weight: 600;
  font-style: normal;
}

body,html{
  background-color: rgb(0, 0, 0);
  height: 100rem;
  margin: 0 15px 0 15px;
  user-select: none;
  font-family: roboto;
}

#navBar{
  display: flex;
  justify-content: center;
  gap: 20px;
}

.headerBtns{
  height: 20px;
  padding: 25px;
  border: 1.5px solid rgb(202, 138, 0);
  color: white;
  background-color: rgb(0, 0, 0);
  border-radius: 10px;
}

.headerBtns:hover{
  background-color: rgb(202, 138, 0);
  cursor: pointer;
  color: black;
}

button{
  font-size: 20px;
  color: rgb(202, 138, 0)
}

#homeContainer{
  width: 100%;
  height: 100%;
}

#content{
  display: flex;
  justify-content: center;
  align-items: start;
  text-align: center;
  width: 100%;
  height: 100%;
  background-color: black;
}

#homeContentContainer{
  background-color: black;

}

#homeTitle, #homeSubtext{
  color: orange;
  padding-bottom: 20px;
  text-align: center;
}

#titleImg {
  display: flex;
  height: 12rem;
  border-radius: 15px;
  align-items: center;
}

#homePagecontainer{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100%;
  height: 30%;

}

#menuContent{
   display: flex;
  width: 100%;
  justify-content: space-evenly;
  flex-direction: column;
  gap: 1%;
  height: 100%;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.644);
  border-radius: 15px;
}

#foodImg1, #foodImg2, #foodImg3{
  height: clamp(50%, 25rem, 20rem);
  width: clamp(50%, 25rem, 20rem);
  border-radius: 25px;
}

.foodImgDiv{
  width: 20rem;
  height: 3rem;
}

#foodItem2Container, #foodItemContainer1, #foodItem3Container{
  display: flex;
  justify-content: left;
  height: 20%;
  background-color: #000000;
  max-width: 50rem;
  color: rgb(202, 138, 0);
  gap: 15px;
  border-radius: 15px;
  box-shadow: 1px 1px 1px 3px rgb(202, 138, 0);
  min-width: 50rem;
}

#contactContent{
  background-color: white;
  height: 100%;
  width: 90%;
  display: flex;
  flex-direction: column;
}

#homeContentContainer{
  padding-top: 5%;
  display: flex;
  height: 20%;
  text-align: center;
  background-color: rgb(0, 0, 0);
  width: 100%;
  justify-content: center;
  align-items: start;
  box-shadow: 1px 1px 2px 1px black;
  gap: 2%;
}

#homePageText{
  padding-top: 1.5%;
}

#homeTitle{
  font-size: 40px;
  font-weight: bold;
}

#homeSubtext{
  font-size: 20px;
}

.foodItemHeader{
  font-size: 30px;
  font-weight: bold;
  padding-bottom: 5%;
  padding-top: 2%;
}

.textContainer{
  width: 50%;
}

#contactContainer3, #contactContainer2, #contactContainer1{
  max-width: 50rem;
  box-shadow: rgb(202, 138, 0) 0px 5px 15px;
  background-color: #000000;
  border-radius: 15px;
  min-width: 30rem;
}

#contactContent{
  padding-top: 10%;
  display: flex;
  align-items: center;
  background-color: rgb(0, 0, 0);
  gap: 3%;
  color: rgb(202, 138, 0);
}
